import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import Modal from 'react-responsive-modal';
import moment from "moment";
import _ from 'lodash';
import { Row, Col } from "react-bootstrap";
import {Animated} from 'react-animated-css';

class ProductItem extends Component {

    constructor(props){
        super(props)

        this.state = {
            open: false,
            stock: 'In Stock',
            quickBuy: false,
            quantity: 1,
            image: null,
            product: null,
            variant: null,
            size: 0
        }
    }

    componentDidMount() {
        const variant = this.props.product.variants
        for (let i=0; i < variant.length; i++) {
            for (let b=0; b < variant[i].selectedOptions.length; b++) {
                if (variant[i].selectedOptions[b].name === "Color") {
                    console.log(variant[i].selectedOptions[b].value)
                    this.setState({variant: variant[i].selectedOptions[b].id})
                }
            }
        }
        this.setState({product: this.props.product, quickBuy: false})
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (!_.isEqual(this.state.product, this.props.product)) {
            this.setState({product: this.props.product, image: null, quickBuy:false})
        }
    }


    onClickHandle(variant) {
        for (let i=0; i < variant.length; i++) {
            for (let b=0; b < variant[i].selectedOptions.length; b++) {
                if (variant[i].selectedOptions[b].name === "Color") {
                    this.setState({variant: variant[i].value})
                }
            }
        }
        this.setState({ image : variant.image.src, variant: variant} );
    }
    onOpenModal = () => {
        this.setState({ open: true });
    };

    onCloseModal = () => {
        this.setState({ open: false });
    };

    minusQty = () => {
        if(this.state.quantity > 1) {
            this.setState({stock: 'In Stock'})
            this.setState({quantity: this.state.quantity - 1})
        }
    }

    plusQty = () => {
        if(this.props.product.stock >= this.state.quantity) {
            this.setState({quantity: this.state.quantity+1})
        }else{
            this.setState({stock: 'Out of Stock !'})
        }
    }
    changeQty = (e) => {
        this.setState({ quantity: parseInt(e.target.value) })
    }

    renderVariants = (product) => {
        const variantList = [];
        if (product.variants.length) {
            const uniqueVariants = [];
            for (let i=0; i < product.variants.length; i++) {

                if (uniqueVariants.length > 3) break;

                if (uniqueVariants.indexOf(product.variants[i].image.src) === -1){
                    uniqueVariants.push(product.variants[i].image.src)
                }
                else continue;

                const temp = (
                    <li className={`grid_thumb_img ${(product.variants[i].image.src === this.state.image) ? 'active' : ''}`}
                        key={i}>
                        <a href="javascript:void(0)" title="Add to Wishlist">
                            <img src={`${product.variants[i].image.src}`}
                                 onClick={() => this.onClickHandle( product.variants[i].id )}/>
                        </a>
                    </li>
                )
                variantList.push(temp);

            }

            return variantList;
        }
    }

    returnMainIMG = (product) => {

        if (this.state.image) {
            return this.state.image;
        }
        else {
            this.setState({image: product.variants[0].image.src})
            if (product.variants.length) {
                if (product.variants[0].image.src) {
                    return product.variants[0].image.src
                }
                else {
                    try {
                        if (product.images[0].src) {
                            return product.images[0].src
                        }
                    }
                    catch {
                        console.log("item " + product.title + " has no image or variant image. rendering nothing")
                        return ""
                    }
                }
            }
        }

    }

    checkIfNew = () => {
        try {
            const createdAt = moment(Date.now());
            const difference = createdAt.diff(this.props.product.createdAt, "hours");
            if (difference < 1344)
                return (<span style={{zIndex:1}} className="lable3">new</span>);
            else return "";
        }
        catch (e){
            console.log(e);
        }
    }

    checkIfSale = () => {
        try {
            const salePrice = this.props.product.variants[0].price;
            const fullPrice = this.props.product.variants[0].compareAtPrice;

            if (!fullPrice) return "";

            const discount = (100 - (salePrice/fullPrice * 100) );

            if (discount > 5)
                return (
                    <div style={{position:"absolute", top:0, left:0, right:0,backgroundColor:"purple", borderBottomLeftRadius:"10px", borderBottomRightRadius:"10px"}}>
                        <p style={{textAlign:"center", color:"white", marginBottom:0, paddingTop:5, paddingBottom:5}}>{Math.round(discount)}% off!</p>
                    </div>
                )
        }
        catch(e) {
            console.log(e);
        }
    }

    referenceSizeChart = (type) => {
        const number = parseInt(type)
        if (number) return number;
        switch (type) {
            case "small":
                return "sm";
            case "medium":
                return "md";
            case "large":
                return "lg";
            case "x-large":
                return "xl";
            case "one size":
                return "n/a"
        }
    }

    renderSizes = () => {
        const sizeBuilder = []
        const product = this.props.product;

        for (let a=0; a < product.options.length; a++) {
            if (product.options[a].name === "Size") {

                for (let i=0; i < product.options[a].values.length; i++) {
                    const value = product.options[a].values[i].value
                    const stringy = this.referenceSizeChart(value.toLowerCase())

                    if (!stringy) continue
                    const temp = (
                        <div className={"quickBuySize"} key={i + a} onClick={() => this.handleAddToCart(value)}
                             style={{
                                 width:30, height:30, overflow:"hidden", margin:5, borderWidth:1, borderRadius:360, borderColor:"black", borderStyle:"solid",
                                 display:"flex", alignItems:"center", justifyContent:"center"
                             }}
                        >
                            <li>
                                <a
                                    style={{padding:10, }}
                                    href={null}>
                                    {stringy}
                                </a>
                            </li>
                        </div>

                    )
                    sizeBuilder.push(temp)
                }
            }
        }
        return (
            <Animated style={{ position:"absolute", bottom:25, left:0, right:0}}
                      animationIn="fadeInRight" animationOut="fadeOutRight" isVisible={this.state.quickBuy}
            >
                <div className={"mx-auto"} style={{backgroundColor:"purple", width:"60%"}}>
                    <p style={{padding:15, textAlign:"center"}}>Choose Your Size</p>
                    <Col xs={10} style={{paddingBottom:15, marginLeft:"auto", marginRight:"auto"}}>
                        <Row style={{justifyContent:"center", flex:1, display:"flex", alignItems:"center"}}>
                            {sizeBuilder}
                        </Row>
                    </Col>
                </div>

            </Animated>
        )
    }

    handleAddToCart = (size) => {
        const product = this.props.product;
        for (let i=0; i < product.variants.length; i++) {
            let sizeIndex = null;
            let colorIndex = null;

            if (product.variants[i].selectedOptions.length === 1) {
                if (product.variants[i].selectedOptions[0].name === "Color") {
                    colorIndex = 0;
                }
                else if (product.variants[i].selectedOptions[0].name === "Size") {
                    sizeIndex = 0;
                }
            }

            if (product.variants[i].selectedOptions.length === 2) {
                if (product.variants[i].selectedOptions[0].name === "Color") {
                    colorIndex = 0;
                }
                else colorIndex = 1

                if (product.variants[i].selectedOptions[1].name === "Size") {
                    sizeIndex = 0;
                }
                else sizeIndex = 1;
            }



            const sizeMatch = product.variants[i].selectedOptions[sizeIndex].value === size
            const colorMatch = product.variants[i].id === this.state.variant
            console.log(sizeMatch + " : " + product.variants[i].selectedOptions[sizeIndex].value)
            console.log(product.variants[i].id  + " : " + this.state.variant)
            if (sizeMatch && colorMatch) {
                product.selectedVariant = i
            }
        }

        if (!product.selectedVariant) {
            console.log("error at")
            console.log(product)
        }
        else {
            this.props.onAddToCartClicked(product, this.state.quantity, this.props.client)
        }

    }

    quickActions = () => {
        return (
            <div onClick={() => {this.setState({quickBuy:true})}} style={{display:"flex", flex:1, width:"80%", height:50}}>
                <Animated style={{flex:1, display:"flex",justifyContent:"center", alignItems:"center"}}
                          animationIn="fadeInUp" animationOut="fadeOutDown" isVisible={true}>
                    <Animated style={{flex:1, display:"flex",justifyContent:"center", alignItems:"center"}}
                              animationIn="fadeInUp" animationOut="fadeOutDown" isVisible={!this.state.quickBuy}>
                        <Row className="quickActions" style={{flex:1, cursor:"pointer"}}>
                            <p style={{textAlign:"center", padding:10}}>Quick Actions</p>
                        </Row>

                    </Animated>


                </Animated>
            </div>
        )
    }




    render() {
        const {product, symbol, onAddToCartClicked, onAddToWishlistClicked, onAddToCompareClicked, client} = this.props;

        let RatingStars = []
        for(var i = 0; i < product.rating; i++) {
            RatingStars.push(<i className="fa fa-star" key={i}></i>)
        }
        return (
                <div className="product-box" onMouseLeave={() => this.setState({quickBuy:false})} style={{display:"flex", flexDirection: "column"}}>
                    <div className="img-wrapper">
                        <div className="lable-block" style={{position:"relative"}}>
                            {this.checkIfNew(product)}
                            {this.checkIfSale(product)}

                        </div>
                        <div className="front">
                            <Link to={`/product/${product.id}`} >
                                <img
                                src={`${this.returnMainIMG(product)}`}
                                style={{height: 480, objectFit:"cover"}}
                                className="img-fluid"
                                alt="" /></Link>
                            {
                                    this.renderSizes()
                            }
                        </div>
                        <div className="cart-info cart-wrap">
                            {this.quickActions()}
                        </div>
                        {product.variants.length ?
                        <ul className="product-thumb-list">
                            {this.renderVariants(product)}
                        </ul>
                            :
                            <ul className="product-thumb-list">
                            </ul>
                        }

                    </div>
                    <div className="product-detail">
                        <div>
                            <div className="rating">
                                {RatingStars}
                            </div>
                            <Link to={`/product/${product.id}`}>
                                <h6 style={{textOverflow:"ellipsis", overflow:"hidden", whiteSpace:"nowrap"}}>{product.title}</h6>
                            </Link>
                            <h4>{symbol}{product.variants[0].price}
                                {
                                    product.variants[0].compareAtPrice &&
                                    <span className="money strikethrough" style={{fontSize:10, padding:10, color:"red"}}>{symbol}{product.variants[0].compareAtPrice}</span>
                                }

                            </h4>
                        </div>
                    </div>
                    <Modal open={this.state.open} onClose={this.onCloseModal} center>
                        <div className="modal-dialog modal-lg modal-dialog-centered" role="document">
                            <div className="modal-content quick-view-modal">
                                <div className="modal-body">
                                    <div className="row">
                                        <div className="col-lg-6  col-xs-12">
                                            <div className="quick-view-img">
                                                <img src={this.returnMainIMG(product)} style={{height:100, width:200}} alt="product images" className="img-fluid" />
                                            </div>
                                        </div>
                                        <div className="col-lg-6 rtl-text">
                                            <div className="product-right">

                                                <h2 style={{textOverflow:"ellipsis", overflow:"hidden", whiteSpace:"nowrap"}}> {product.title} </h2>
                                                <h3>{symbol}{product.variants[0].price}</h3>
                                                {/*product.variants?
                                                <ul className="color-variant">
                                                    {product.variants.map((vari, i) => {
                                                        return <li className={vari.color} key={i} title={vari.color} onClick={() => this.onClickHandle(vari.images)}></li>
                                                    })

                                                    }
                                                </ul>:''*/}
                                                <div className="border-product">
                                                    <h6 className="product-title">product details</h6>
                                                    <p>{product.description}</p>
                                                </div>
                                                <div className="product-description border-product my-3">
                                                    {product.size?
                                                    <div className="size-box">
                                                        <ul>
                                                            {product.size.map((size, i) => {
                                                                return <li key={i}><a href={null}>{size}</a></li>
                                                            })}
                                                        </ul>
                                                    </div>:''}
                                                    <h6 className="product-title" style={{marginTop:25, marginBottom:25}}>quantity</h6>
                                                    <div className="qty-box">
                                                        <div className="input-group">
                                                              <span className="input-group-prepend">
                                                                <button type="button" className="btn quantity-left-minus" onClick={this.minusQty} data-type="minus" data-field="">
                                                                 <i className="fa fa-angle-left"></i>
                                                                </button>
                                                              </span>
                                                            <input type="text" name="quantity my-3" value={this.state.quantity}  onChange={this.changeQty} className="form-control input-number" />
                                                                <span className="input-group-prepend">
                                                                <button type="button" className="btn quantity-right-plus" onClick={this.plusQty} data-type="plus" data-field="">
                                                                <i className="fa fa-angle-right"></i>
                                                                </button>
                                                               </span>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className="product-buttons">
                                                    <button  className="btn btn-solid" onClick={() => this.handleAddToCart()} >add to cart</button>
                                                    <Link to={`/product/${product.id}`} className="btn btn-solid">view detail</Link>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </Modal>
                </div>
        )
    }
}

export default ProductItem;
