class Entry extends React.Component {
    constructor() {
        super();
        this.state = {
            products: [],
            loading: true,
            sizingProblems: []
        }
    }

    componentDidMount() {
        this.loadProducts();
        this.props.setClientObject(client);
        this.props.openCartSidebar(false)
 //      this.setState({products: data});
        this.windowResize()
        this.loadCart()
        window.addEventListener('resize', this.windowResize);
    }

    loadCart = () => {
        const storedCheckout = localStorage.getItem("checkout")
        if (storedCheckout) {
            client.checkout.fetch(storedCheckout).then((checkout) => {
                if (!checkout) {
                    localStorage.removeItem("checkout")
                    client.checkout.create().then((checkout) => {
                        localStorage.setItem("checkout", checkout.id)
                        this.props.updateCart({product: [], sum:0})
                    })
                }
                else {
                    this.props.updateCart(checkout)
                }
            })
        }

    }



    windowResize = () => {
        const newSize = {
            width: window.outerWidth,
            height: window.outerHeight
        }
        this.props.setWindowSize(newSize)
    }

    componentWillUnmount() {
    }

    loadAllImages = () => {
        const imgs = document.querySelectorAll(".img-fluid")
        for (let i=0; i < imgs.length; i++) {
            const aspectRatio = Math.round(imgs[i].clientWidth / imgs[i].clientHeight)
            if (aspectRatio !== 1)
                console.log(imgs[i].src)

        }

    }

    loadProducts = async () => {
        const response = await client.product.fetchAll();
        const newResponse = this.fixDataResponseIssues(response);
        this.props.setProducts(newResponse);
        //delete this
/*        for (let i=0; i < response.length; i++) {
            try {
                if (!response[i].variants[0].price) {
                    console.log("Error will happen at")
                    console.log(response[i])
                }
            }
            catch {
                console.log("Error will happen at")
                console.log(response[i])
            }
        }
        //*/
        this.setState({products: response}, () => {
            if (response[response.length - 1].hasNextPage) {
                this.loadProductsInBackground();
            }
        })
    }

    fixDataResponseIssues = (response) => {
        const fixedResponse = []
        for (let i=0; i < response.length; i++) {
            try {
                for (let a=0; a < response[i].options.length; a++) {
                    if (response[i].options[a].name === "Size") {
                        const values = response[i]
                        for (let b=0; b < response[i].options[a].values.length; b++) {
                            const value = response[i].options[a].values[b].value
                            if (value.includes("/")) {
                                const temp = this.state.sizingProblems;
                                const caseCheck = value.toLowerCase().trim()
                                if (caseCheck.includes("small")) {
                                    const newCall = {
                                        errorLocated: "Sizing Error Detected at Index " + i + " On Item : " + response[i].title,
                                        fixed: "updated " + response[i].options[a].values[b].value + " to " + "Small"
                                    }
                                    temp.push(newCall)
                                    values.options[a].values[b].value = "Small";

                                }
                                if (caseCheck.includes("large")) {
                                    const newCall = {
                                        errorLocated: "Sizing Error Detected at Index " + i + " On Item : " + response[i].title,
                                        fixed: "updated " + response[i].options[a].values[b].value + " to " + "Large"
                                    }
                                    temp.push(newCall)
                                    values.options[a].values[b].value = "Large";
                                }
                                const split = caseCheck.split("/")
                                if (split.length > 1) {
                                    const parse = parseInt(split[0])
                                    if (parse) {
                                        const newCall = {
                                            errorLocated: "Sizing Error Detected at Index " + i + " On Item : " + response[i].title,
                                            fixed: "updated " + response[i].options[a].values[b].value + " to " + split[0]
                                        }
                                        temp.push(newCall)
                                        values.options[a].values[b].value = split[0];
                                    }
                                }

                                this.setState({sizingProblems: temp})

                            }
                        }
                        //this app depends on the variant category as shopify puts all their prices and checkout w/item information in variants
                        try {
                            if (!response[i].variants[0])
                                continue;
                        }
                        catch {
                            continue;
                        }

                        if (values.length > 0) {
                            fixedResponse.push(values);
                        }
                        else {
                            fixedResponse.push(response[i])
                        }

                    }
                }
            }
            catch {
                console.log("Error will happen at")
                //console.log(response[i])
            }

        }
        return fixedResponse;
    }


    loadProductsInBackground = async () => {
        const model = await client.graphQLClient.fetchNextPage(this.state.products, {args: {first: 45}});
        const response = model.model;

        const newResponse = this.fixDataResponseIssues(response)

        const productList = this.state.products;
        const combineArr = productList.concat(newResponse);

        this.setState({
            products: combineArr
        }, () => {
            if (response[response.length - 1].hasNextPage) {
                this.loadProductsInBackground();
            }
            else {
                if (this.state.sizingProblems.length > 0)
                    console.log("Sizing problems were detected. See below")
                    console.log(this.state.sizingProblems)
                this.props.setProducts(combineArr);
            }
        })

    }


    render() {

        return(
            <Container style={{padding:0, margin:0}} fluid>
                <IntlProvider translations={translations} locale='en'>
				<BrowserRouter basename={'PrettyGirlStores/'} >
					<ScrollContext>
                        <Layout>
                            {/*Sidebar Cart*/}
                            <CartPopout/>
						<Switch>
                            <Route exact path={`/`}><Redirect to={"/home"}/></Route>

                                {/*Routes For Layouts*/}
                                <Route path={`/home`} component={Fashion}/>

								{/*Routes For Features (Product Collection) */}
								<Route path={`/collections/:collection`} component={CollectionLeftSidebar}/>
                                <Route path={`/products/:productType`} component={CollectionLeftSidebar}/>
                                <Route path={`/deals/:productType`} component={CollectionLeftSidebar}/>

								{/*Routes For Single Product*/}
								<Route path={`/product/:id`} component={LeftSideBar}/>
                            {/*<Route path={`/right-sidebar/product/:id`} component={RightSideBar}/>
								<Route path={`/no-sidebar/product/:id`} component={NoSideBar}/>
								<Route path={`/col-left/product/:id`} component={ColumnLeft}/>
								<Route path={`/col-right/product/:id`} component={ColumnRight}/>
								<Route path={`/accordian/product/:id`} component={Accordian}/>
								<Route path={`/column/product/:id`} component={Column}/>
								<Route path={`/left-image/product/:id`} component={LeftImage}/>
								<Route path={`/right-image/product/:id`} component={RightImage}/>
								<Route path={`/vertical/product/:id`} component={Vertical}/>*}


								{/*Routes For custom Features*/}
                            {/*<Route path={`/cart`} component={Cart}/>*/}
								<Route path={`/wishlist`} component={wishList}/>
								<Route path={`/compare`} component={Compare}/>
								<Route path={`/checkout`} component={checkOut}/>
								<Route path={`/order-success`} component={orderSuccess}/>

								<Route path={`/sales/orders`} component={aboutUs}/>

								{/*Routes For Extra Pages*/}
                                <Route path={`/pages/about-us`} component={aboutUs}/>
                                <Route path={`/pages/404`} component={PageNotFound}/>
                                {/*<Route path={`/pages/lookbook`} component={lookbook}/>*/}
                                <Route path={`/pages/login`} component={Login}/>
                                <Route path={`/pages/register`} component={Register}/>
                                <Route path={`/pages/search`} component={Search}/>
                                <Route path={`/pages/collection`} component={Collection}/>
                                <Route path={`/pages/forget-password`} component={ForgetPassword}/>
                                <Route path={`/pages/contact`} component={Contact}/>
                                <Route path={`/pages/dashboard`} component={Dashboard}/>
                                <Route path={`/pages/faq`} component={Faq}/>

								{/*Features*/}
								{/*Theme Elements*/}
                            {/*<Route path={`/features/element-title`} component={ElementTitle}/>
                                <Route path={`/features/element-banner`} component={ElementBanner}/>
                                <Route path={`/features/element-slider`} component={ElementSlider}/>
                                <Route path={`/features/element-category`} component={ElementCategory}/>
                                <Route path={`/features/element-service`} component={ElementService}/>
                                <Route path={`/features/element-ratio`} component={ElementRatio}/>*/}

								{/*Product Elements*/}
                            {/*<Route path={`/features/element-product-box`} component={ElementProductBox}/>
                                <Route path={`/features/element-product-slider`} component={ElementProductSlider}/>
                                <Route path={`/features/element-product-no-slider`} component={ElementProductNoSlider}/>
                                <Route path={`/features/element-product-multiple-slider`} component={ElementMultipleSlider}/>
                                <Route path={`/features/element-product-tab`} component={ElementProductTab}/>*/}

								{/*Portfolios*/}
                                {/*<Route path={`/features/portfolio-grid/:columns`} component={GridCols}/>
                                <Route path={`/features/portfolio-masonary/:columns`} component={MasonaryGridCols}/>

								{/*Blog Pages*/}
                            {/*<Route path={`/blog/right-sidebar`} component={RightSide}/>
                                <Route path={`/blog/details`} component={Details}/>
                                <Route path={`/blog/blog-page`} component={BlogPage}/>*/}


                            <Route component={PageNotFound} />
                         </Switch>
                        </Layout>
					  </ScrollContext>
					</BrowserRouter>
                </IntlProvider>
            </Container>
    	);
    }
}



