import React, {Component} from 'react';
import {Helmet} from 'react-helmet'
import Breadcrumb from "../common/breadcrumb";
import NewProduct from "../common/new-product";
import Filter from "./common/filter";
import FilterBar from "./common/filter-bar";
import ProductListing from "./common/product-listing";
import StickyBox from "react-sticky-box";
import {Link} from "react-router-dom";

class CollectionLeftSidebar extends Component {
    constructor() {
        super();
        this.state = {
            layoutColumns:3,
            pageType:"",
            location: "",
        }
    }

    componentDidMount() {
        const location = this.props.location.pathname;
        this.setState({location: location})
        const path = location.split("/")
        if (path.includes("products")) {
            this.setState({pageType: "products"})
        }
        if (path.includes("collections")) {
            this.setState({pageType: "collections"})
        }
        if (path.includes("deals")) {
            this.setState({pageType: "deals"})
        }
    }

    checkRoute = () => {

    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        const location = this.props.location.pathname;
        if (location !== this.state.location) {
            this.setState({location: location})
            const path = location.split("/")
            if (path.includes("products")) {
                this.setState({pageType: "products"})
            }
            if (path.includes("collections")) {
                this.setState({pageType: "collections"})
            }
            if (path.includes("deals")) {
                this.setState({pageType: "deals"})
            }
        }
    }

    LayoutViewClicked(colums) {
        this.setState({
            layoutColumns:colums
        })
    }

    openFilter = () => {
        document.querySelector(".collection-filter").style = "left: -15px";
    }

    renderRouteText = () => {
        const location = this.props.location.pathname;
        switch (location) {
            case "/products/tops":
                return (
                    <div className="top-banner-content small-section">
                        <h3>Shop Tops</h3>
                        <h5>We only feature the best brands and designers!</h5>
                    </div>
                )
            case "/products/bottoms":
                return (
                    <div className="top-banner-content small-section">
                        <h3>Shop Bottoms</h3>
                        <h5>Now with more bottom, for your bottoms!</h5>
                    </div>
                )
            case "/products/dresses":
                return (
                    <div className="top-banner-content small-section">
                        <h3>Shop Dresses</h3>
                        <h5>Search through our entire dress catalog!</h5>
                    </div>
                )
            case "/products/bodysuits":
                return (
                    <div className="top-banner-content small-section">
                        <h3>Shop Bodysuits</h3>
                        <h5>Search through our entire bodysuit catalog!</h5>
                    </div>
                )
            case "/products/jackets":
                return (
                    <div className="top-banner-content small-section">
                        <h3>Shop Jackets</h3>
                        <h5>Search through our entire jacket catalog!</h5>
                    </div>
                )
            case "/products/jumpsuits":
                return (
                    <div className="top-banner-content small-section">
                        <h3>Shop Jumpsuits</h3>
                        <h5>Search through our entire jumpsuit and romper catalog!</h5>
                    </div>
                )
            case "/products/shoes":
                return (
                    <div className="top-banner-content small-section">
                        <h3>Shop Shoes</h3>
                        <h5>Search through our entire shoes catalog!</h5>
                    </div>
                )
            case "/collections/winter":
                return (
                    <div className="top-banner-content small-section">
                        <h3>Winter Collection</h3>
                        <h5>Our winter collection.</h5>
                    </div>
                )
            case "/collections/fall":
                return (
                    <div className="top-banner-content small-section">
                        <h3>Fall Collection</h3>
                        <h5>Our fall collection.</h5>
                    </div>
                )
            case "/collections/summer":
                return (
                    <div className="top-banner-content small-section">
                        <h3>Summer Collection</h3>
                        <h5>Our summer collection.</h5>
                    </div>
                )
            case "/deals/tops":
                return (
                    <div className="top-banner-content small-section">
                        <h3>$15 and under</h3>
                        <h5>Check out our top collection!</h5>
                    </div>
                )
            case "/deals/bottoms":
                return (
                    <div className="top-banner-content small-section">
                        <h3>$15 and under</h3>
                        <h5>Check out our bottom collection!</h5>
                    </div>
                )
            case "/deals/dresses":
                return (
                    <div className="top-banner-content small-section">
                        <h3>$15 and under</h3>
                        <h5>Check out our dress collection!</h5>
                    </div>
                )
            case "/deals/bodysuits":
                return (
                    <div className="top-banner-content small-section">
                        <h3>$15 and under</h3>
                        <h5>Check out our bodysuit collection!</h5>
                    </div>
                )
            case "/deals/jackets":
                return (
                    <div className="top-banner-content small-section">
                        <h3>$15 and under</h3>
                        <h5>Check out our jacket collection!</h5>
                    </div>
                )
            case "/deals/jumpsuits":
                return (
                    <div className="top-banner-content small-section">
                        <h3>$15 and under</h3>
                        <h5>Check out our jumpsuit collection!</h5>
                    </div>
                )
            case "/deals/shoes":
                return (
                    <div className="top-banner-content small-section">
                        <h3>$15 and under</h3>
                        <h5>Check out our shoe collection!</h5>
                    </div>
                )
            case '/collections/new-arrivals':
                return (
                    <div className="top-banner-content small-section">
                        <h3>New Products</h3>
                        <h5>Our newest products!</h5>
                    </div>
                )
            case "/collections/dresses":
                return (
                    <div className="top-banner-content small-section">
                        <h3>New Dresses</h3>
                        <h5>Our newest dresses!</h5>
                    </div>
                )
            case "/collections/clothes":
                return (
                    <div className="top-banner-content small-section">
                        <h3>New Clothes</h3>
                        <h5>Our newest clothes!</h5>
                    </div>
                )
            case "/collections/jeans":
                return (
                    <div className="top-banner-content small-section">
                        <h3>New Jeans</h3>
                        <h5>Check out our jeans collection!</h5>
                    </div>
                )
            case "/collection/best-sellers":
                return (
                    <div className="top-banner-content small-section">
                        <h3>New and Popular</h3>
                        <h5>The newest and hottest products! </h5>
                    </div>
                )

        }
    }

    render (){
        return (
            <div>
                {/*SEO Support*/}
                <Helmet>
                    <title>Pretty Girl Store | Collection of Products</title>
                    <meta name="description" content="Pretty Girl Stores - Pretty Girl have offered incredible designs, stylish clothes, and magnificent modern looks for the budget minded fashionista. Serving our adoring customers in any one of our 25 Pretty Girl Stores throughout the Tri-State area, Pretty Girl prides itself on taking fast fashion to the next level at incredibly low prices." />
                </Helmet>
                {/*SEO Support End */}

                <Breadcrumb title={'Collection'}/>

                <section className="section-b-space">
                    <div className="collection-wrapper">
                        <div className="container">
                            <div className="row">
                                <div className="col-sm-3 collection-filter">

                                    <StickyBox offsetTop={20} offsetBottom={20}>
                                        <div>
                                            {this.state.pageType !== "deals" &&
                                                <Filter location={this.props.location}/>
                                            }
                                            <NewProduct/>
                                            <div className="collection-sidebar-banner">
                                                <a href="#">
                                                    <img src={`${process.env.PUBLIC_URL}/assets/images/side-banner.png`} className="img-fluid" alt="" />
                                                </a>
                                            </div>
                                        </div>
                                    </StickyBox>
                                    {/*side-bar banner end here*/}
                                </div>
                                <div className="collection-content col">
                                    <div className="page-main-content ">
                                        <div className="">
                                            <div className="row">
                                                <div className="col-sm-12">
                                                    <div className="top-banner-wrapper">
                                                        <a href="#">
                                                            {/*<img src={`${process.env.PUBLIC_URL}/assets/images/mega-menu/2.jpg`} className="img-fluid" alt=""/>*/}
                                                        </a>
                                                            {this.renderRouteText()}
                                                    </div>
                                                    <div className="collection-product-wrapper">
                                                        <div className="product-top-filter">
                                                            <div className="container-fluid p-0">
                                                                <div className="row">
                                                                    <div className="col-xl-12">
                                                                        <div className="filter-main-btn">
                                                                            <span onClick={this.openFilter}
                                                                                className="filter-btn btn btn-theme"><i
                                                                                className="fa fa-filter"
                                                                                aria-hidden="true"></i> Filter</span>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                                <div className="row">
                                                                    <div className="col-12">
                                                                        <FilterBar location={this.props.location} onLayoutViewClicked={(colmuns) => this.LayoutViewClicked(colmuns)}/>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>

                                                        {/*Products Listing Component*/}
                                                        <ProductListing colSize={this.state.layoutColumns} location={this.props.location}/>

                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>

            </div>
        )
    }
}

export default CollectionLeftSidebar;
