
import React, { Component } from 'react';
import Slider from 'react-slick';
import ImageZoom from "./image-zoom";

class SmallImages extends Component {
    constructor (props) {
        super(props);
        this.state = {
            nav2: null
        }
    }
    componentDidMount() {
        this.setState({
            nav2: this.slider2
        });
    }

    renderVariants = () => {
        const item = this.props.item;
        const imgBuilder = [];
        const uniqueImages = [];
        try {
            if (item.variants.length) {
                for(let i=0; i < item.variants.length; i++) {
                    if (uniqueImages.indexOf(item.variants[i].image.src) === -1) {
                        uniqueImages.push(item.variants[i].image.src)
                        const temp = <div  key={i} onClick={() => this.props.setVariant(item.variants[i].selectedOptions[1].value)}>
                            <ImageZoom  image={item.variants[i].image.src} />
                        </div>
                        imgBuilder.push(temp)
                    }
                }
            }
            else {
                return []
            }
            if (imgBuilder.length < 2) return []
            return imgBuilder;
        }
        catch {
            return []
        }
    }

    render() {
        const { item, settings } = this.props;

        var productsnav = settings;

        return (
            <div className="row">
                <div className="col-12 p-0">
                    <Slider {...productsnav} asNavFor={this.props.navOne} ref={slider => (this.slider2 = slider)} className="slider-nav">
                        {
                            this.renderVariants()
                        }
                    </Slider>
                </div>
            </div>
        );
    }
}

export default SmallImages;
